body {
  background: #f0f0f5 !important;
}



.logon-container {
  width: 100%;

  max-width: 1120px;
  height: 100vh;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;

  .form {
    width: 90%;
    max-width: 350px;
    margin-right: 30px;

    img {
      width: 100%;
      max-width: 300px;
      margin: 0 auto;
    }

    form {
      margin-top: 50px;
      margin-bottom: 12px;

      h1 {
        font-size: 32px;
        margin-bottom: 22px;
        font-weight: 300;
        color: #c70365;

      }

      input {
        margin-bottom: 8px;
      }
    }
  }
}

@import './responsive';